html, body, div, span,
h1, h2, h3, h4, h5, h6, p, a,ol, ul, li,form, label,
table,article, aside, canvas,
figure, figcaption, footer, header,
nav, section{
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
*, *::after, *::before {
    box-sizing: border-box;
}

a{
	text-decoration: none;
}

button{
	background: 0;
	border: 0;
	padding: 0;
	cursor: pointer;
}

fieldset{
	padding: 0;
	border: 0;
	margin: 0;
}
